import React from 'react'
import {Button} from 'pasharp'
import {ButtonFormatter} from './Button.style'

const PrimaryButton = () => {
    return (
        <ButtonFormatter>
            <Button>Submit</Button>
            <Button type='primary'>Submit</Button>
        </ButtonFormatter>
    )
}

export default PrimaryButton



