import styled from 'styled-components'

export const DefaultButtonStyle = styled.div`
    ${({props}) => {
        const {type} = props
        // console.log(type)
        switch (type) {
            case 'primary':
                return (
                    `
                    .btn {
                        padding: 10px 20px;
                        background-color: #008CBA;
                        color: #fff;
                        transition: all .5s;
                    }
                    .btn:hover {
                        background-color: #0078a0;
                        cursor: pointer;
                    }
                    .btn:active {
                        background-color: #0078a0;
                        cursor: pointer;
                    }
                    `
                )
            default:
                return (
                    `
                    .btn {
                        padding: 10px 20px;
                        background-color: #E7E7E7;
                        color: #000;
                        transition: all .5s;
                    }
                    .btn:hover {
                        background-color: #c4c4c4;
                        cursor: pointer;
                        color: #fff;
                    }
                    .btn:active {
                        background-color: #c4c4c4;
                        cursor: pointer;
                        color: #fff;
                    }
                    `
                )
        }
    }}
`


